import { Injectable } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';

import { PhotoLibraryService } from './photo-library.service';
import { PhotoLibrary } from './photo-library';


@Injectable()
export class PhotoAlbumResolver implements Resolve<PhotoLibrary> {

  constructor(private photoLibraryService: PhotoLibraryService) {}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<PhotoLibrary> {
    const id: number = route.params.id;
    return this.photoLibraryService.getItem({id: id});
  }
}
