import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { PhotoLibrary } from 'src/app/services/photo-library/photo-library';
import { SidebarService } from 'src/app/services/sidebar/sidebar.service';
import { ImagesService } from 'src/app/services/images/images.service';

@Component({
  selector: 'app-photoalbum-page',
  templateUrl: './photoalbum-page.component.html',
  styleUrls: ['./photoalbum-page.component.scss']
})
export class PhotoalbumPageComponent implements OnInit {
  public photos: PhotoLibrary[] = null;
  public album: PhotoLibrary = null;
  public showSidebar: Boolean = false;

  constructor(private route: ActivatedRoute,
    private sidebarService: SidebarService,
    private imagesService: ImagesService) {
    this.sidebarService.handleSetSettings({showSidebar: false, bgColor: '#fff'});
  }

  public getFileNameAndExtension(path) {
    const str = path
      .split('\\')
      .pop()
      .split('/')
      .pop();
    const ext = str.substr((Math.max(0, str.lastIndexOf('.')) || Infinity) + 1);
    const name = str.replace('.' + ext, '');
    return {
      ext,
      name,
      full: `${name}.${ext}`
    };
  }

  ngOnInit() {
    this.photos = this.route.snapshot.data.photos.sort((n1: PhotoLibrary, n2: PhotoLibrary) => {
        if (n1.photoIndex > n2.photoIndex) {
            return 1;
        }
        if (n1.photoIndex < n2.photoIndex) {
            return -1;
        }
        return 0;
    });
    this.album = this.route.snapshot.data.album;
  }

  public prepareLink(link: string) {
    return this.imagesService.prepareBackgroundUrl(link);
  }
}
