import { Injectable } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';

import { IListItemsPage } from '../list-items-service';
import { PhotoLibraryService } from './photo-library.service';
import { PhotoLibrary } from './photo-library';
import { contentTypes } from 'src/app/constants/content-types';

@Injectable()
export class PhotoAlbumsResolver implements Resolve<IListItemsPage<PhotoLibrary>> {

  constructor(private photoLibraryService: PhotoLibraryService) {}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<IListItemsPage<PhotoLibrary>> {
    return this.photoLibraryService.getItemsPaged({ filter: `ContentType eq '${encodeURI(contentTypes.album)}'`,
    top: 8, orderBy: [['slAlbumIndex', true]] });
  }
}
