import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { PhotogalleryPageComponent } from './components/photogallery-page/photogallery-page.component';
import { PhotoalbumPageComponent } from './components/photoalbum-page/photoalbum-page.component';

import { PhotoAlbumsResolver } from 'src/app/services/photo-library/photo-albums.resolver';
import { PhotosResolver } from 'src/app/services/photo-library/photos.resolver';
import { PhotoAlbumResolver } from 'src/app/services/photo-library/photo-album.resolver';

const routes: Routes = [
  {
    path: '',
    component: PhotogalleryPageComponent,
    resolve: { albums: PhotoAlbumsResolver },
    data: { animation: 'fadeAnimation', title: 'Альбомы' }
  },
  {
    path: ':id',
    component: PhotoalbumPageComponent,
    resolve: { photos: PhotosResolver, album: PhotoAlbumResolver },
    data: { animation: 'fadeAnimation', title: 'Альбом' }
  }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    providers: [PhotosResolver, PhotoAlbumResolver, PhotoAlbumsResolver],
    exports: [RouterModule]
})
export class PhotogalleryRoutingModule { }
