import { Component, OnInit, Inject, OnDestroy } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { ActivatedRoute } from '@angular/router';

import { CookieService } from 'ngx-cookie-service';

import { IListItemsPage } from 'src/app/services/list-items-service';
import { PhotoLibrary } from 'src/app/services/photo-library/photo-library';
import { SidebarService } from 'src/app/services/sidebar/sidebar.service';
import { PortalService } from 'src/app/services/portal/portal.service';
import { PhotoLibraryService } from 'src/app/services/photo-library/photo-library.service';
import { contentTypes } from 'src/app/constants/content-types';

@Component({
  selector: 'app-photogallery-page',
  templateUrl: './photogallery-page.component.html',
  styleUrls: ['./photogallery-page.component.scss']
})
export class PhotogalleryPageComponent implements OnInit, OnDestroy {

  public page: IListItemsPage<PhotoLibrary> = null;
  public albums: PhotoLibrary[] = [];
  public showSidebar: Boolean = false;

  private s4 = null;
  private isLoadingItems: Boolean = false;

  constructor(
    @Inject(DOCUMENT) private document: Document,
    private route: ActivatedRoute,
    private sidebarService: SidebarService,
    private sharepointService: PortalService,
    private photoLibraryService: PhotoLibraryService,
    private cookieService: CookieService
  ) {
    this.sidebarService.handleSetSettings({ showSidebar: false, bgColor: '#fff' });
  }

  loadMore() {
    this.isLoadingItems = true;
    this.page.getNext().subscribe((page: IListItemsPage<PhotoLibrary>) => {
      this.page = page;
      this.albums = this.albums.concat(this.page.items);
      this.isLoadingItems = false;
    });
  }

  load() {
    this.photoLibraryService.getItemsPaged({ filter: `ContentType eq '${contentTypes.album}'`,
      top: 8, orderBy: [['slAlbumIndex', true]] })
      .subscribe((page: IListItemsPage<PhotoLibrary>) => {
        this.page = page;
        this.albums = page.items;
        this.isLoadingItems = false;
        document.getElementById('s4-workspace').scrollTop = 0;
      });
  }

  onWorkSpaceScroll() {
    if (
      this.s4.scrollTop >=
      this.s4.scrollHeight - (this.s4.clientHeight + 100)
    ) {
      if (this.page.hasNext && !this.isLoadingItems) {
        this.loadMore();
      }
    }
  }

  ngOnInit() {
    this.s4 = this.document.getElementById('s4-workspace');
    this.s4.addEventListener('scroll', this.onWorkSpaceScroll.bind(this), true);
    this.page = this.route.snapshot.data.albums;
    this.albums = this.page.items;
  }

  ngOnDestroy(): void {
    this.s4.removeEventListener('scroll', this.onWorkSpaceScroll.bind(this), true);
  }

  create() {
    this.cookieService.set('splnu', '0', 1, '/');
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `/slPhotoLibrary/Forms/Upload.aspx?ContentTypeId=0x012000BFB863A2672645ABBEA198EF8334172D&Source=`
      + encodeURIComponent(window.location.href);
  }

  edit(item: any) {
    this.cookieService.set('splnu', '0', 1, '/');
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `/slPhotoLibrary/Forms/EditForm.aspx?ID=${item.id}&Source=`
      + encodeURIComponent(window.location.href);
  }

  remove(item: any) {
    this.photoLibraryService.recycleItem(item)
      .subscribe(() => this.load());
  }
}
